/**
 * Action pour ajouter un nouvel employ au store Redux.
 *
 * @param {Object} employee - L'objet reprsentant un employ avec toutes ses donnes.
 * @returns {Object} Une action Redux avec le type 'ADD_EMPLOYEE' et l'employ  ajouter comme payload.
 */
export const addEmployee = (employee) => {
    return {
        type: 'ADD_EMPLOYEE',
        payload: employee,
    };
};